:root {
  --cl-black: #1e1e1e;
  --cl-white: #ffffff;
  --cl-gray: #bfbfbf;
  --cl-red: #bf2e37;
}

html {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  padding: 20px;
  background-color: var(--cl-black);
  color: var(--cl-white);
  line-height: 1.3;
}

@font-face {
  font-family: "OpenSans";
  src: url("../app/assets/fonts/OpenSans-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "OpenSans";
  src: url("../app/assets/fonts/OpenSans-Light.ttf") format("truetype");
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: "OpenSans";
  src: url("../app/assets/fonts/OpenSans-Bold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "OpenSansCondensed";
  src: url("../app/assets/OpenSans_SemiCondensed-Light.ttf") format("truetype");
  font-weight: 300;
  font-style: normal;
}
body {
  font-family: "OpenSans", sans-serif;
  font-weight: 300;
  font-size: 18px;
  color: var(--cl-white);
}

.h1 {
  font-size: 42px;
}

.h2 {
  font-size: 24px;
}

.text-light {
  font-weight: 300;
}

.text-bold {
  font-weight: 700;
}

.uppercase {
  text-transform: uppercase;
}